import React, { useState, useContext, useEffect, useCallback } from 'react'
import styled, { keyframes } from 'styled-components'
import { Color, ColorRgb, InputStyle, NextBtn } from "../theme"
import { FaRegEye, FaRegEyeSlash } from 'react-icons/fa'
import Modal from 'react-awesome-modal';
import { CgProfile } from 'react-icons/cg'
import { GoMail } from 'react-icons/go'
import { BiLockOpen } from 'react-icons/bi'
import { AiOutlineSend } from 'react-icons/ai'
import { MdKeyboardArrowDown } from 'react-icons/md'
import UserContext from '../../context/UserContext'
import PasswordInducator from './PasswordIndicator'
import axios from '../../axiosbase';
import SEctor from 'containers/users/Sector'

const isNumberRegx = /\d/;
const specialCharacterRegx = /[ ~@#$!%^&*()_+\-=[\]{;':\\|,.<>/?]/;

function Signup() {
  const signUpCtx = useContext(UserContext);
  const [showSectors, setShowSectors] = useState(false);
  const [selectSectors, setSelectSectors] = useState("-  -");
  const [sectorId, setSectorId] = useState(null);
  const [Show, setShow] = useState(false);
  const [Show2, setShow2] = useState(false);
  const [sectorData, setSectorData] = useState([]);
  const [PassText, setPassText] = useState("");
  const [scale, setScale] = useState("1");
  const [visible, setVisible] = useState(false);
  const [visible2, setVisible2] = useState(false);
  // const [passwordFocused, setPasswordFocused] = useState(false);
  const [color1, setColor1] = useState("rgba(0,0,0,0.7)");
  const [reg, setReg] = useState('');

  const registerHandle = (e) =>{
    if(e.target.value.length < 8){
      setReg(e.target.value);
    }
  }

  const [password, setPassword] = useState("");
  const [passwordValidity, setPasswordValidity] = useState({
    minCar: null,
    number: null,
    specialChar: null
  });

  const openModal = () => { setVisible(false); setVisible2(true); }
  const closeModal = () => { setVisible(false); }
  const closeModal3 = () => { setVisible2(false); setVisible(false); }
  const closeModal2 = () => {
    let confirm = document.getElementById("GetcheckBtn2").checked;
    if (confirm === true) { setVisible2(false); setVisible(true); } else { setColor1("red"); }
  }

  const keyPress = useCallback(e => {
    if (e.key === 'F2') { openModal(); }
  }, []);

  useEffect(() => {
    axios.get(`business-sector`).then(res => setSectorData(res.data.data));
    document.addEventListener('keydown', keyPress);
    return () => document.removeEventListener('keydown', keyPress)
  }, [keyPress]);

  const onChangePassword = password => {
    setPassword(password);
    setPasswordValidity({
      minChar: password.length >= 8 ? true : false,
      number: isNumberRegx.test(password) ? true : false,
      specialChar: specialCharacterRegx.test(password) ? true : false
    });
  }

  const handleClick = () => {
      let rs = document.querySelectorAll(".userInp"); let arr = Array.from(rs); let finalOne = {};
      arr.forEach(element => {
        if (element.value !== "") {
          element.classList = - " red"
          element.classList += " userInp"
          let field = element.name; let value = element.value; finalOne[field] = value;
        } else { element.classList += " red" }
      });
      let keys = Object.keys(finalOne);
      if (keys.length < 6) {
        setPassText("  "); setScale("1"); setTimeout(() => { setScale("0"); setPassText(null);  }, 3000);
      }else if(reg.length !== 7){
        setPassText("    "); setScale("1"); setTimeout(() => { setScale("0"); setPassText(null);  }, 3000);
      }else if (selectSectors === "-  -") {
        setPassText("  "); setScale("1"); setShowSectors(true); setTimeout(() => { setScale("0"); setPassText(null); }, 3000);
        // if(selectSectors!=="-  -"){
        //   setPassText("");setScale("0");
        // }
      } else if (passwordValidity.minChar === false || passwordValidity.number === false || passwordValidity.specialChar === false) {
        setPassText("     .."); setScale("1"); setTimeout(() => { setScale("0"); setPassText(null); }, 3000);
      } else if (finalOne.password !== finalOne.passwordagain) {
        setPassText("    ..."); setScale("1"); setTimeout(() => { setScale("0"); setPassText(null); }, 3000);
      } else {
        finalOne["business_sectorId"] = sectorId; setPassText(""); signUpCtx.signUpUser(finalOne); setScale("1");
      }
  }
  const cond = signUpCtx.errMsgSignup.cond;

  return (
    <Component className="SignUp">
      {showSectors && <GhostPar><div onClick={() => setShowSectors(false)} className="Ghost"></div> <div className="Sectorpar"><SEctor data={sectorData} setSectorId={setSectorId} setSelectSectors={setSelectSectors} setShowSectors={setShowSectors} /></div></GhostPar>}
      <span >   
            {/* <Switch>
                <Link to="/signup"><a><span className="SignBtn">  </span></a></Link>
            </Switch> */}
        <a><span className="SignBtn" onClick={openModal} >  </span></a>  .</span>
      {/* <form onSubmit={handleClick}> */}
      <Modal visible={visible2} width="800" height="90%" effect="fadeInDown" onClickAway={closeModal3} >
        <div className="Modaltest">
          <div className="ModalTextPar">
            <div className="redPAr">
              <div className="redTitle">
                           ,   :
              </div>
              <div className="redDesc">
                     , ,           :
              </div>
            </div>

            <div className="mainText">
              <div className="title">   </div>
              <ul className="desc">
                <li>   ,    ,      ,    ;</li>
                <li> ,        ( ,   , ,  , , ,  , ,      ,   ,    );</li>
                <li>     (, ,    ,    ,   ,    ,  ,    ,          ,    ,   ,      ,  , ,     ,   , , , , )</li>
                <li>      ,                  (    10     ); </li>
                <li>    ()-    </li>
               
                
              </ul>
            </div>

            <div className="mainText">
              <div className="title">   ,  : </div>
              <ul className="desc">
                <li>     ,       ;</li>
                <li>  :    (, , );  (   );     ;    , ;  ; ;</li>
                <li>    300        ; -;</li>
                <li>       ;</li>
                <li> ,           (CITES)-    ,  ;</li>
                <li>,        ;  ,  , , ; -       ;</li>
                <li>      ;    , , ;  ,   ;</li>
                <li> ,           ,  ;      0.005      ,   ;</li>
                <li>    ;    ,     ; , ,       (     )  ,   ; ,  (     );</li>
                <li>-     ,  .</li>
              </ul>
            </div>

            <div className="btnPar">
              <span style={{ color: color1 }} className="text">        ..</span>
              <input name="confirm" id="GetcheckBtn2" className="check" type="checkbox" />
              <button onClick={closeModal2} className="btn btn-primary"> </button>
            </div>

          </div>
        </div>
      </Modal>

      <Modal visible={visible} width="900" height="580" effect="fadeInDown" onClickAway={closeModal}  >
          <div className="formOneParent">
            <div className="headPar"><span className="headText"></span>
              <a style={{cursor:"pointer"}} onClick={closeModal}>X</a>
            </div>

            {signUpCtx.errMsgSignup.cond === false ? (<div className="inputPar">
              <div className="UserSectionMiddle">
                <div className="inpChild">
                  <div className="labels"><span>  :</span> </div>
                  <div className="name">
                    <InputStyle className="newInp">
                      <input type="input" className="userInp form__field" placeholder="..." name="companyname" required />
                      <div className="line"></div>
                    </InputStyle>
                    {/* <div className="form__group">
                                                          <input type="input" className="userInp  form__field" name="companyname" required />
                                                      </div> */}
                  </div>
                </div>
                <div className="inpChild">
                  <div className="labels"><span>  :</span> </div>
                  <div className="name">
                    <InputStyle className="newInp">
                      <input type="number" value={reg} onChange={registerHandle} className="userInp form__field" placeholder="123..." name="companyregister" required />
                      <div className="line"></div>
                    </InputStyle>
                  </div>
                </div>
                <div className="inpChild sectorChild">
                  <div className="labels"><span> : </span> </div>
                  <div className="name">
                    <div onClick={() => setShowSectors(prev => !prev)} className="sectors" ><span>{selectSectors} </span>  <MdKeyboardArrowDown /> </div>
                  </div>
                </div>
              </div>

              <div className="UserSection">
                <div className="inpChild">
                  <div className="labels"><span> :</span> </div>
                  <div className="name">
                    <CgProfile />
                    <InputStyle className="newInp">
                      <input type="input" className="userInp form__field" placeholder="..." name="name" required />
                      <div className="line"></div>
                    </InputStyle>
                  </div>
                </div>
                <div className="inpChild">
                  <div className="labels"><span>Email :</span> </div>
                  <div className="name">
                    <GoMail />

                    <InputStyle className="newInp">
                      <input type="email" className="userInp  form__field" placeholder=" " name="email" required />
                      <div className="line"></div>
                    </InputStyle>

                  </div>
                </div>
              </div>

              <div className="UserSection">
                <div className="inpChild">
                  <div className="labels">
                    <span>   </span>
                    <span className="forget"> 8-   </span>
                  </div>
                  <div className="name">
                    <BiLockOpen />
                    <InputStyle className="newInp pass">
                      <input
                      //  onFocus={() => setPasswordFocused(true)} onBlur={() => setPasswordFocused(false)}
                       onChange={e => onChangePassword(e.target.value)} value={password} type={Show ? 'text' : 'password'} className="userInp  form__field" placeholder=" " name="password" />
                      {Show ? <FaRegEye onClick={() => setShow(false)} /> : <FaRegEyeSlash onClick={() => setShow(true)} />}
                      <div className="line"></div>
                    </InputStyle>

                  </div>
                  <PasswordInducator validity={passwordValidity} />
                </div>

                <div className="inpChild">
                  <div className="labels"> <span>    </span> </div>
                  <div className="name">
                    <BiLockOpen />
                    <InputStyle className="newInp pass">
                      <input type={Show2 ? 'text' : 'password'} className="userInp  form__field" placeholder="   " name="passwordagain" />
                      {Show2 ? <FaRegEye onClick={() => setShow2(false)} /> : <FaRegEyeSlash onClick={() => setShow2(true)} />}
                      <div className="line"></div>
                    </InputStyle>
                  </div>
                </div>
              </div>

              <div className="SubmitButtonPar">
                <NextBtn style={cond ? { width: `20%`, opacity: `0.7` } : { width: `100%`, opacity: `1` }} onClick={handleClick} className="SubmitButton" type="button">{cond ? <img src="/gif1.gif" alt="" /> : ``}  <div style={cond ? { display: `none` } : { display: `flex` }} className="flexchild"><AiOutlineSend /> <AiOutlineSend className="hide" /> <AiOutlineSend className="hide1" /></div>  </NextBtn>
                {PassText ?
                (<span className="colorText" style={{ transform: `scale(${scale})` }}>{PassText}</span>)
                :(<span className="colorText" style={{ transform: `scale(${scale})` }}>{signUpCtx.errMsgSignup.msg}</span>)}
              </div>
            </div>)
              : (<div className="success">
                <h3 className="title"> </h3>
                <span className="desc">{signUpCtx.errMsgSignup.msg}</span>
              </div>)}

          </div>
      </Modal>
      {/* </form> */}
    </Component>
  )
}

export default Signup

const sectorAnimate = keyframes`
  0%{ transform:translateY(120px); opacity:0; }
  80%{ transform:translateY(-20px); opacity:0.8; }
  100%{ transform:translateY(0); opacity:1; }
`

const GhostPar = styled.div`
  position:fixed;
  top:0;
  left:0;
  height:100vh;
  width:100vw;
  z-index:10010;
  .Ghost{
    background-color:rgba(250,250,250,0.3);
    backdrop-filter: blur(4px);
    position:fixed;
    height:100%;
    width:100%;
  }
  .Sectorpar{
    box-shadow:1px 1px 17px -6px rgba(0,51,102,1);
    animation: ${sectorAnimate} 0.6s ease;
    transition:all 0.3s ease;
    transform:scale(1);
    top:10%;
    left:25%;
    position:absolute;
    z-index:10;
  }
  @media only screen and (max-width:768px){
    .Sectorpar{
      top:10%;
      left:5%;
    }
  }
`

const Component = styled.div`
    
    margin-top:15px;
    text-align:center;
    .Modaltest{
      width:100%;
      height:100%;
      overflow-y:scroll;
      background-color:white;
      .ModalTextPar{
        color:black;
        font-size:13px;
        padding:20px 50px;
        .btnPar{
          border-top:1px solid rgba(0,0,0,0.1);
          padding:20px 0px;
          padding-bottom:30px;
          width:100%;
          display:flex;
          flex-direction:row;
          align-items:center;
          justify-content:flex-end;
          .text{
            font-size:13px;
            color:black;
            padding-right:10px;
            color:#339CFF;
          }
          .check{
            cursor:pointer;
            height:25px;
            width:25px;
            margin-right:30px;
          }
          button{

          }
        }
        .mainText{
          margin-bottom:20px;
          text-align:start;
          .title{
            margin-bottom:6px;
            font-size:14px;
            font-weight:500;
            .nemelt{
              font-size:13px;
              font-weight:400;
            }
          }
          .desc{
            list-style-position: outside;
            list-style-type: circle;
            margin-left:30px;
            li{
              margin:7px 0px;
            }
          }
        }
        .redPAr{
          margin-bottom:15px;
          .redTitle{
            font-size:15px;
            font-weight:500;
            color:#FF1300;
            margin-bottom:15px;
          }
          .redDesc{
            text-align:start;
            color:#FF4333;
          }
        }


      }
    }
    span{
        color:rgba(0,0,0,0.8);
        font-size:15px;
      }   
      a{
        text-decoration: none;
        .SignBtn{
            cursor:pointer;
            color:${Color};
        }
    }

      .formOneParent{
            border-top:4px solid ${Color};
            background-color:white;
            border-radius:5px;
            margin-bottom:16px;
            padding-bottom:20px 0px;
            padding:10px 60px;
            .success{
              padding:50px 0px;
              .title{
                color:${Color};
                padding:15px 0px;
              }
              .desc{
                font-size:16px;
              }
            }

            .headPar{
            padding:6px 0;
            font-size:1.3rem;
            border-bottom:1px solid rgba(63, 81, 181,0.3);
            color:black;
            display:flex;
            flex-direction:row;
            justify-content:space-between;
            .headText{
                font-size:20px;
            }
          }
        .inputPar{
            display:flex;
            flex-direction:column;
            align-items:flex;
            justify-content:center;
            .UserSectionMiddle{
              display:flex;
              algin-items:center;
              justify-content:space-between;
              .inpChild{
                .labels{
                  span{
                    font-size:14px;
                    color:rgba(0,0,0,0.8);
                  }
                }
              }
                .sectorChild{
                  position:relative;
                  width:25%;
                  .sectors{
                    display:flex;
                    justify-content:space-between;
                    align-items:center;
                    font-weight:500;
                    cursor:pointer;
                    border:1px solid rgba(0,51,102,0.3);
                    width:100%;
                    border-radius: 2px;
                    transtion:all 0.3s ease;
                    overflow:hidden;
                    padding:8px 0px;
                    padding-left:0px;

                    span{ 
                      margin-top:-4px;
                      height:22px;
                      width:100%;
                      font-size:13px !important;
                      // padding:8px 0px;
                      padding-left:3px;
                    }
                    &:hover{
                      box-shadow:1px 1px 8px -4px rgba(0,51,102,1);
                    }
                    svg{
                      font-size:22px;
                      margin-right:0px;
                      margin-bottom:0px;
                    }
                  }
                }
            }
            .UserSection{
              display:flex;
              flex-direction:row;
              justify-content:space-between;
              .passIndPar{
                // margin-top:15px;
                text-align:start;
                font-size:13px;
              }
              .inpChild{
                width:46%;
                  margin:12px 0px;
                  display:flex;
                  flex-direction:column;
                  .labels{
                      display:flex;
                      flex-direction:row;
                      justify-content:space-between;
                      font-size:13px;
                      span{
                          font-size:14px;
                          color:rgba(0,0,0,0.8);
                          font-weight:500;
                      }
                      .forget{
                       color:rgba(${ColorRgb},0.9);
                       font-weight:600;
                          cursor:pointer;
                          &:hover{
                               color:rgba(${ColorRgb},0.7);
                          }
                      }
                  }
               .name{
                   display:flex;
                   flex-direction:row;
                   align-items:flex-end;
                   justify-content:flex-end;
                   width:100%;
                   svg{
                     color:rgba(${ColorRgb},0.7);
                     font-size:24px;
                     margin-right:15px;
                     margin-bottom:5px;
                   }
                   .pass{
                        svg{
                          position:absolute;
                          right:0;
                          bottom:2px;
                          font-size:18px;
                          color:rgba(0,0,0,0.9);
                          cursor:pointer;
                        }
                    }
                   .form__group{
                    position:relative;
                    padding: 15px 0 0;
                    margin-top: 0px;
                    width: 100%;
                   }
                   
                 }
              }
            }
           
          
         
        }
        .SubmitButtonPar{
            margin-top:12px;
            margin-bottom:20px;
            text-align:end;
            display:flex;
            justify-content:center;
            align-items:center;
            flex-direction:column;
            .colorText{
              width:100%;
              text-align:center;
              background-color: #f6c343;
              box-shadow:1px 1px 10px -2px black;
              border-radius:5px;
              font-size:15px;
              font-weight:400;
              color:black;
              margin-top:18px;
              line-height:34px;
            }
            .colorTextgreen{
              background-color:green;
              box-shadow:1px 1px 10px -2px black;
              border-radius:5px;
              font-size:15px;
              font-weight:400;
              color:white;
              margin:16px 0;
              line-height:24px;
              padding:3px 15px; 
            }
         
            .SubmitButton{
                width:100%;
                font-size:14px;
            }
          }
        }
     
      @media only screen and (max-width:768px){
            .formOneParent{
              width:100%;
              height:100%;
              overflow-y:scroll;
              padding:10px 18px;
              .SubmitButtonPar{
                flex-direction: column-reverse;
              }
                .inputPar{
                  .UserSectionMiddle{
                    flex-direction: column;
                  }
                }
                .headPar{
                    font-size:1em;
                    .headText{
                        font-size:14px;
                    }
                }
              .inputPar{
                .UserSection{
                  flex-direction:column;
                  .inpChild{
                    width:100%;
                  }
                }
                .UserSectionMiddle{
                  .sectorChild{
                    width:100%;
                  }
                }
              }
            }
      }
      
`