import React, { useState } from 'react'
import axios from '../../axiosbase';
import { Email, Item, renderEmail, Box,Image} from 'react-html-email'

function EmailSend2() {
    const [ color, setColor ] =useState("#FFFF00");
    const [ Text , setText ] = useState("  ");
    const [ Scale , setScale ] = useState("0");
    const clickHandle = async () =>{
       await axios.post('send-pps-notice',  {
            // html: Html,
            email: "yondooo61@gmail.com",
            emailBody:EmailHTML,
           }).then((res)=>{
                console.log(res.data.success, "my Response");
                if(res.data.success === true){
                    setColor("#32CD32");
                    setText(" ");
                    setScale("1");
                }else{
                    setColor("#FFFF00");
                    setText("  ");
                    setScale("1");
                }
              }).catch((e)=>{
                console.log(e.response, "err Response");  
                if(e.response.data.success === false){
                    setColor("#FFFF00");
                    setText("  ");
                    setScale("1");
                }
          });
      };
    return (
        <div style={{textAlign:"center"}}>
            <div style={{marginTop:20, width:"100%"}}>
                <div style={{transition:`all 0.4s ease`,marginBottom:"15px", backgroundColor:color, padding:"5px 0px", transform:`scale(${Scale})`}}>{Text}</div>
                <Email className="bigTitle" title="EDP">
                    <Image style={{width:"100%"}} src="http://www.edp.mn/Content/Images/mn-MN/head.jpg" />
                        <Item style={{border:"1px solid rgba(0,0,0,0.2)",padding:'20px 20px',backgroundColor:"#f2f2f2"}} align="end">
                        <Box style={{textAlign:"center",width:"100%", margin:'5px 0px',fontWeight:'500', fontSize:'14px'}} >     </Box>
                        <Box style={{textAlign:"end",width:"100%", margin:'20px 0px', fontSize:'12px'}}> :  Example company :</Box>

                        <Box style={{textAlign:"start",width:"100%", fontSize:'12px'}}> []:  984685465</Box>
                        <Box style={{textAlign:"start",width:"100%", fontSize:'12px'}}> []:  2019/05/10</Box>

                        <Box style={{textAlign:"start",width:"100%", margin:'20px 0px',fontWeight:'500', fontSize:'12px'}}>:     </Box>
                        <Box style={{textAlign:"start",width:"100%", margin:'10px 0px', fontSize:'12px'}}>   </Box>

                        <Box style={{textAlign:"start",width:"100%", margin:'18px 0px',textIndent: "50px", fontSize:'12px'}}>              </Box>
                        <Box style={{textAlign:"start",width:"100%", margin:'18px 0px',textIndent: "50px", fontSize:'12px'}}>                     .         .</Box>
                        <Box style={{textAlign:"start",width:"100%", margin:'18px 0px',textIndent: "50px", fontSize:'12px'}}>                ,        . </Box>
                        
                        <Box style={{textAlign:"start",width:"100%", margin:'18px 0px', fontSize:'12px'}}>    :</Box>
                        
                        <Box style={{textAlign:"start",width:"100%", margin:'18px 0px', fontSize:'12px',fontWeight:'500'}}> :      .............</Box>

                        <Box style={{textAlign:"start",width:"100%", margin:'18px 0px', fontSize:'12px',fontWeight:'500'}}> :  .............</Box>
                        
                        </Item>
                </Email>
        </div>
        <button onClick={clickHandle} style={{margin:"20px", width:"20%"}} className="btn btn-success"></button>
    </div>
    )
}

export default EmailSend2


