import React, {useState, useEffect} from 'react'
import styled from 'styled-components'
import {fontFamily, textColor, ColorRgb, } from '../../theme';
import {CgPushChevronUp, CgPushChevronDown} from 'react-icons/cg'


function ModalTwo(props) {
    const [ DataOne, setDataOne ] = useState([]);
    const [ margin, setMargin ] = useState(0);

    useEffect(()=>{
        if(props.Data2){ const finalData = [];
            tableData.map((el,i)=>{
                props.Data2.map((elem,index)=>{ if (i  === index){ el["names"] = elem.names; el["files"] = elem.files; el["getDate"] = elem.getDate ; el["recentDate"] = elem.recentDate} });
                finalData.push(el);
            }); setDataOne(finalData);
        }else{ setDataOne(tableData); }
    },[props.Data2]);

    const clickHandle = (el) =>{  if(el==="add"){ setMargin(prev => prev + 10); }else{  if(margin < 0){  setMargin(prev =>prev);  }else{ setMargin(prev => prev - 10);  }  }}

    return (
        <TableTwo style={{marginBottom:margin}} >
            <h6>2. //   *</h6>
            <div className="table container">
                <div  className="Header row">
                        <div className="col-md-3 col-sm-3 col-3"><div className="question"></div></div>
                        <div style={{borderLeft:`1px solid rgba(0,0,0,0.3)`}} className="col-md-3 col-sm-3 col-3"><div className="question">(,  ,    )     </div></div>
                        <div style={{textAlign:"center",borderLeft:`1px solid rgba(0,0,0,0.3)`}} className="col-md-4 col-sm-4 col-4"> 
                            <div style={{marginTop:13, borderTop:`1px solid rgba(0,0,0,0.3)`}} className="row">
                                <div className="col-md-6 col-sm-6 col-6"> </div>
                                <div style={{borderLeft:`1px solid rgba(0,0,0,0.3)`}}  className="col-md-6 col-sm-6 col-6"></div>
                            </div>
                        </div>
                        <div className="col-md-2 col-sm-2 col-2" style={{borderLeft:`1px solid rgba(0,0,0,0.3)`}}><div className="question">   /</div></div>
                </div>

                {DataOne.map((el,i)=>{
                    return(
                        <div key={i} className="items row">
                                <div style={{borderBottom:`1px solid rgba(0,0,0,0.2)`,backgroundColor:` rgba(63,81,181,0.1)`}} className="col-md-3 col-sm-3 col-3"><div className="question">{el.items}</div></div>
                                <div style={{borderLeft:`1px solid rgba(0,0,0,0.2)`,borderBottom:`1px solid rgba(0,0,0,0.2)`,backgroundColor:`rgba(63,255,181,0.2)`}} className="col-md-3 col-sm-3 col-3"><div className="question">{el.name}</div></div>
                                <div style={{textAlign:"center",borderLeft:`1px solid rgba(0,0,0,0.2)`,borderBottom:`1px solid rgba(0,0,0,0.2)`,backgroundColor:`rgba(63,255,181,0.2)`}} className="col-md-4 col-sm-4 col-4">
                                    <div style={{height:`100%`}} className="row">
                                        <div className="col-md-6 col-sm-6 col-6"><div className="question">{el.getDate}</div> </div>
                                        <div style={{borderLeft:`1px solid rgba(0,0,0,0.2)`, }}  className="col-md-6 col-sm-6 col-6"><div className="question">{el.recentDate}</div></div>
                                    </div>
                                </div>
                                <div className="col-md-2 col-sm-2 col-2" style={{ overflow:`hidden`,borderLeft:`1px solid rgba(0,0,0,0.2)`,borderBottom:`1px solid rgba(0,0,0,0.2)`,backgroundColor:`rgba(63,255,181,0.2)`}}><div style={{ justifyContent:`start`}} className="question">{el.files? <span></span>: <span></span>}</div></div>
                        </div>
                    )
                })}
            </div>
            <div className="MarginBtn"> {margin>0?<CgPushChevronUp onClick={()=>clickHandle("remove")} />:<div></div>} <CgPushChevronDown onClick={()=>clickHandle("add")} /></div>
        </TableTwo>
    )
}

export default ModalTwo

const TableTwo  = styled.div`
  
    transition:all 0.2s ease;
    // margin-top:40px;
    color:rgb(${textColor});
    font-family:${fontFamily};
    padding: 50px 20px 0px 20px;
    .table{
        margin-top:10px;
        border:1px solid rgba(0,0,0,0.2);
        .Header{
            background-color:rgba(${ColorRgb},0.8);
            color:white;
            .question{
                padding-top:6px;
                padding-bottom:6px;
                display:flex;
                justify-content:center;
                align-items:center;
                height:100%;
            }
        }
        .items{
            .question{
                padding-top:6px;
                padding-bottom:6px;
                display:flex;
                justify-content:center;
                align-items:center;
                height:100%;
            }
        }
    }
    @media print{
        break-inside: avoid;
        padding: 50px 10px 50px 5px;
        .MarginBtn{
            display:none;
        }
    } 
`

const tableData = [
    { items: "    (,   )", list:[]},
    {items: "   ", list:[]},
    {items: " ",list:[]},
    {items: "  ",list:["","",""]},
    {items: " ",list:["  ( : ,  , ,  ) ",""]},
    {items: "  ,   (, , ,     )",list:[]},
    {items: "  ",list:[]},
    {items: " ,  ",list:[]},
    {items: "  ",list:[]},
    {items: " /   , ",list:[]},
  ];