const dateNow = new Date()
const currentYear = dateNow.getFullYear()
const currentMonth = dateNow.getMonth() + 1
const currentDate = dateNow.getDate()

const initialContract = {
   year: null,
   month: null,
   day: null,
   contract_number: null,
   order_year: null,
   order_month: null,
   order_day: null,
   order_number: null,
   hot_aimag: null,
   sum_duureg: null,
   horoo_bag: null,
   detailed_location: null,
   state_reg: null,
   register_no: null,
   company_name: null,
   funding: null,
   funding_verbose: null,
   location: null,
   bh_zuvluh: null
}

const initialSignersEdp = [{
   name: '.',
   position: ',   ,       ',
   signature: null,
   order: 1,
   category: 'edp',
   page: 'contract'
}, {
   name: '.',
   position: '   ',
   signature: null,
   order: 2,
   category: 'edp',
   page: 'contract'
}, {
   name: '.',
   position: '   ',
   signature: null,
   order: 3,
   category: 'edp',
   page: 'contract'
}, {
   name: 'M.',
   position: '      ',
   signature: null,
   order: 4,
   category: 'edp',
   page: 'contract'
}]

const initialSignersReceiving = [{
   name: null,
   position: null,
   signature: null,
   order: 1,
   category: 'receiving',
   page: 'contract'
}, {
   name: null,
   position: null,
   signature: null,
   order: 2,
   category: 'receiving',
   page: 'contract'
}]

const descriptions = [
   '/   ./',
   '/    ./'
]

export default function MakeContract() {
   const AlertCtx = useContext(AlertContext)

   const projectId = useQuery().get('projectId')
   const userId = useQuery().get('userId')

   const [info, setInfo] = useState(initialContract)

   const handleInput = (key, value) => setInfo(prev => ({ ...prev, [key]: value }))

   const [signersEdp, setSignersEpd] = useState(initialSignersEdp)
   const [signersReceiving, setSignersReceiving] = useState(initialSignersReceiving)

   const [user, setUser] = useState({})

   useEffect(() => {
      if (projectId === null || projectId === undefined) {
         return
      }
      if (userId === null || userId === undefined) {
         return
      }
      axios.get(`users/${userId}`, {
         headers: { Authorization: getLoggedUserToken() }
      }).then(res => {
         setUser(res.data.data)

         axios.get(`contracts?projectId=${projectId}`, {
            headers: { Authorization: getLoggedUserToken() }
         }).then(res => {
            const { signers, ...info } = res.data.data
            setInfo(prev => ({ ...prev, ...info }))
            if ((signers ?? []).length) {
               setSignersEpd(signers.filter(signer => signer.category === 'edp'))
               setSignersReceiving(signers.filter(signer => signer.category === 'receiving'))
            }
            AlertCtx.setAlert({ open: true, variant: 'success', msg: ' .' })
         }).catch(err => {
            if (err.response.status === 490) {
               setInfo(prev => ({
                  ...prev,
                  year: prev.year || currentYear,
                  month: prev.month || currentMonth,
                  day: prev.day || currentDate,
                  register_no: prev.register_no || (user.companyregister ?? null),
                  company_name: prev.company_name || (user.companyname ?? null)
               }))
               return
            }
            AlertCtx.setAlert({ open: true, variant: 'error', msg: '  .' })
         })
      })
   }, [projectId])

   const componentRef = useRef()

   const handlePrint = useReactToPrint({
      content: () => componentRef.current
   })

   const handleSave = () => {
      if (info.id === undefined) {
         axios.post(`contracts?projectId=${projectId}`, {
            contract: info,
            signers: [...signersEdp, ...signersReceiving]
         }, {
            headers: { Authorization: getLoggedUserToken() }
         }).then(res => {
            const { signers, ...info } = res.data.data
            setInfo(prev => ({ ...prev, ...info }))
            if ((signers ?? []).length) {
               setSignersEpd(signers.filter(signer => signer.category === 'edp'))
               setSignersReceiving(signers.filter(signer => signer.category === 'receiving'))
            }
            AlertCtx.setAlert({ open: true, variant: 'success', msg: ' .' })
         }).catch(err => {
            AlertCtx.setAlert({ open: true, variant: 'error', msg: '  .' })
         })
      } else {
         axios.put(`contracts/${info.id}`, {
            contract: info,
            signers: [...signersEdp, ...signersReceiving]
         }, {
            headers: { Authorization: getLoggedUserToken() }
         }).then(res => {
            const { signers, ...info } = res.data.data
            setInfo(prev => ({ ...prev, ...info }))
            if ((signers ?? []).length) {
               setSignersEpd(signers.filter(signer => signer.category === 'edp'))
               setSignersReceiving(signers.filter(signer => signer.category === 'receiving'))
            }
            AlertCtx.setAlert({ open: true, variant: 'success', msg: '  .' })
         }).catch(err => {
            AlertCtx.setAlert({ open: true, variant: 'error', msg: '   .' })
         })
      }
   }

   return (
      <div className="tw-bg-white tw-rounded-lg tw-shadow-md tw-p-2 tw-border-t tw-border-gray-100">
         <div className="tw-flex tw-justify-end">
            <button className="tw-mt-2 tw-mr-2 tw-flex tw-items-center tw-bg-blue-800 tw-text-white tw-py-1 tw-px-5 tw-rounded hover:tw-shadow-md active:tw-bg-blue-700 focus:tw-outline-none tw-transition-colors tw-transition-shadow tw-font-light" onClick={handlePrint}>
               <span className="tw-text-sm">  </span>
               <PrintSVG className="tw-w-5 tw-h-5 tw-ml-2" />
            </button>
         </div>

         <div className="tw-pb-10" ref={componentRef}>
            <div className="tw-py-6 print-break-after">
               <div className="tw-text-lg tw-font-medium tw-text-center tw-mt-6 tw-mx-2 sm:tw-mx-8">
                   
               </div>

               <div className="tw-mx-2 sm:tw-mx-10 tw-mt-6 tw-pb-4">
                  <div className="tw-flex tw-flex-wrap tw-justify-between tw-px-2 sm:tw-px-4">
                     <span className="">
                        <Fill value={info.year} name="year" setter={handleInput} editable defaultLength={4} number />  <Fill value={info.month} name="month" setter={handleInput} editable defaultLength={2} number />   <Fill value={info.day} name="day" setter={handleInput} editable defaultLength={2} number />- 
                     </span>
                     <span className="tw-mx-2">
                         : <Fill value={info.contract_number} name="contract_number" setter={handleInput} editable />
                     </span>
                     <span className="">
                         
                     </span>
                  </div>

                  <div className="tw-mt-6 tw-leading-relaxed" style={{ wordSpacing: 2 }}>
                     <p className="" style={{ textIndent: 16 }}>
                           (  )-            2016  08   26-    ,     15  ,         <Fill value={info.order_year} name="order_year" setter={handleInput} editable defaultLength={4} number />  <Fill value={info.order_month} name="order_month" setter={handleInput} editable defaultLength={2} number />   <Fill value={info.order_day} name="order_day" setter={handleInput} editable defaultLength={2} number />-  <Fill value={info.order_number} name="order_number" setter={handleInput} editable defaultLength={4} />     ,
                     </p>
                     <p className="">
                           ,  ,  , 2  ,  , 8     ,   ,          (    ),
                     </p>
                     <p className="">
                           , <Fill value={info.hot_aimag} name="hot_aimag" setter={handleInput} editable placeholder="/" />, <Fill value={info.sum_duureg} name="sum_duureg" setter={handleInput} editable placeholder="/" />, <Fill value={info.horoo_bag} name="horoo_bag" setter={handleInput} editable placeholder="/" />, <Fill value={info.detailed_location} name="detailed_location" setter={handleInput} editable placeholder=" " />     <Fill value={info.state_reg} name="state_reg" setter={handleInput} editable defaultLength={12} /> ,  <Fill value={info.register_no} name="register_no" setter={handleInput} editable defaultLength={12} />  <Fill value={info.company_name} name="company_name" setter={handleInput} editable defaultLength={16} /> (    )  (    )   .
                     </p>
                     <p className="" style={{ textIndent: 16 }}>
                            <Fill value={info.company_name} />-         ,   ,     ,     ,  ,     .
                     </p>
                  </div>
               </div>

               <div className="tw-mx-2 sm:tw-mx-8 tw-mt-8">
                  {Object.entries(contract).map(([key, value]) =>
                     <div className="" key={key}>
                        <Header header={`${key}  . ${value.category?.toUpperCase()}`} key={key} />
                        {value.provisions.map((provision, i) =>
                           <Provision
                              order={provision.order}
                              provision={{
                                 '2.8': <span>
                                                     50   <Fill value={info.funding} name="funding" setter={handleInput} defaultLength={12} editable number /> (<Fill value={info.funding_verbose} name="funding_verbose" setter={handleInput} defaultLength={24} editable /> ) / /     ."
                                 </span>,
                                 '6.9.1': <span>
                                         : <Fill value={info.location} name="location" setter={handleInput} defaultLength={36} editable />
                                 </span>
                              }[provision.order] || provision.provision}
                              subLevel={provision.subLevel}
                              key={i}
                           />
                        )}
                     </div>
                  )}
               </div>

               <div className="tw-text-base tw-text-center tw-mt-8 tw-font-medium tw-mx-2 sm:tw-mx-8">
                   :
               </div>

               <div className="tw-mt-6 tw-flex tw-flex-wrap tw-mx-2 sm:tw-mx-10">
                  <div className="sm:tw-w-1/2">
                     <div className="tw-p-2 tw-font-medium">
                           :
                     </div>

                     {signersEdp.map(signer =>
                        <Sign
                           signer={signer}
                           setter={setSignersEpd}
                           key={signer.order}
                        />
                     )}
                  </div>

                  <div className="sm:tw-w-1/2">
                     <div className="tw-p-2 tw-font-medium">
                           :
                     </div>

                     {signersReceiving.map((signer, i) =>
                        <SignReceiving
                           signer={signer}
                           setter={setSignersReceiving}
                           description={descriptions[i]}
                           key={signer.order}
                        />
                     )}
                  </div>

                  <div className="print-no-break">
                     <Fill value={info.bh_zuvluh} name="bh_zuvluh" setter={handleInput} editable defaultLength={30} />
                     <p className="">
                             
                     </p>
                  </div>
               </div>

               <div className="tw-flex tw-justify-center">
                  <button className="tw-my-8 tw-bg-blue-800 tw-text-white tw-font-light tw-text-15px tw-rounded tw-py-2 tw-px-8 hover:tw-shadow-md active:tw-bg-blue-700 focus:tw-outline-none tw-transition-colors print-invisbile" onClick={handleSave}>
                     
                  </button>
               </div>
            </div>

            <ActivityPlanAttach contractId={info.id} />
            <FinalCostAttach contract={info} user={user} />
            <ProtectionReport contract={info} />
            <PerformanceReport contract={info} user={user} />
            <OwnershipAttach contract={info} user={user} />
            <PurchasePlanAttach contractId={info.id} />
            <ContractAttach5 contractId={info.id} />
            <ContractAttach6 contractId={info.id} />
         </div>
      </div>
   )
}

export function Header({ header }) {
   return (
      <div className="tw-text-base tw-font-medium tw-text-center tw-mt-6 tw-mb-2">
         {header}
      </div>
   )
}

export function Provision({ order, provision, subLevel }) {
   return (
      <div className="tw-flex tw-p-2 print-no-break" style={{ marginLeft: (+subLevel || 0) * 24 }}>
         {order &&
            <div className="tw-mr-4">
               {order}
            </div>
         }
         <div className="">
            {provision}
         </div>
      </div>
   )
}

export function Fill({ value, name, index, setter, editable = false, defaultLength, dotted = false, placeholder, number = false }) {
   return editable
      ? <span className="tw-relative tw-inline-block">
         <span className="tw-invisible tw-leading-snug tw-mx-0.5">
            {value || (placeholder ?? '_'.repeat(defaultLength ?? 10))}
         </span>

         <input className="tw-absolute tw-top-0 tw-left-0 tw-w-full tw-rounded-none tw-border-b-2 tw-border-gray-700 tw-border-dotted focus:tw-outline-none tw-box-border tw-leading-snug tw-placeholder-opacity-30 tw-mx-0.5 print-no-border" type={number ? 'number' : 'text'} value={value ?? ''} onChange={e => setter(name, e.target.value, index)} placeholder={placeholder} />
      </span>
      : <span className={`tw-leading-snug tw-mx-0.5 ${dotted && 'tw-border-b-2 tw-border-gray-700 tw-border-dotted tw-box-border'} print-no-border`}>
         {value}
      </span>
}

function Sign({ signer, setter }) {
   return (
      <div className="tw-p-2 tw-h-44 print-no-break">
         <p className="">
            {signer.name}
         </p>
         <p className="tw-mt-1 tw-max-w-sm">
            {signer.position}
         </p>
         <Signature signer={signer} setter={setter} />
      </div>
   )
}

function SignReceiving({ signer, description, setter }) {
   const handleInput = (key, value) => {
      setter(prev => {
         const next = [...prev]
         const index = next.findIndex(item => item.order === signer.order)
         next[index][key] = value
         return next
      })
   }

   return (
      <div className="tw-p-2 tw-h-44 print-no-break">
         <div className="">
            <span className="tw-mr-2 print-hide">
               :
            </span>
            <Fill value={signer.name} name="name" setter={handleInput} editable defaultLength={24} />
         </div>
         <div className="mt-1">
            <span className="tw-mr-2 print-hide">
                :
            </span>
            <Fill value={signer.position} name="position" setter={handleInput} editable defaultLength={24} />
         </div>
         <p className="tw-font-light print-hide">
            {description}
         </p>
         <Signature signer={signer} setter={setter} />
      </div>
   )
}

export function Signature({ signer, setter }) {
   const [sigModalOpen, setSigModalOpen] = useState(false)
   const [hovered, setHovered] = useState(false)

   const sigCanvasRef = useRef()

   const handleDrawSignature = () => {
      setter(prev => {
         const next = [...prev]
         const index = next.findIndex(item => item.order === signer.order)
         next[index].signature = sigCanvasRef.current.getTrimmedCanvas().toDataURL('image/png')
         return next
      })
   }

   const handleClearSignature = () => {
      sigCanvasRef.current.clear()
      setter(prev => {
         const next = [...prev]
         const index = next.findIndex(item => item.order === signer.order)
         next[index].signature = null
         return next
      })
   }

   return (
      <div className="tw-flex tw-flex-wrap tw-mt-1">
         <div className="tw-relative">
            {signer.signature
               ? <img src={signer.signature} alt=" " className={`${classSignature} tw-object-scale-down print-border-bottom`} onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)} onClick={() => setSigModalOpen(true)} />
               : <div className={classSignature} onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)} onClick={() => setSigModalOpen(true)} />
            }
            <PenAltSVG className={`tw-absolute tw-top-1/2 tw-left-1/2 tw--translate-x-1/2 tw-w-7 tw-h-7 tw-text-gray-600 tw-transform-gpu ${hovered ? 'tw--translate-y-1/2 tw-opacity-100' : 'tw--translate-y-3/4 tw-opacity-0'} tw-transition-all tw-duration-300 tw-cursor-pointer`} onMouseEnter={() => setHovered(true)} onClick={() => setSigModalOpen(true)} />
         </div>

         <ModalWindow modalOpen={sigModalOpen} setModalOpen={setSigModalOpen}>
            <div className="tw-p-2 tw-flex tw-flex-col">
               <div className="tw-text-sm tw-mb-2">
                    :
               </div>

               <SignaturePad canvasProps={{ className: 'tw-rounded tw-border tw-border-gray-400', width: 624, height: 192 }} ref={sigCanvasRef} onEnd={handleDrawSignature} />

               <div className="tw-flex tw-justify-center tw-mt-4">
                  <button className="tw-rounded tw-text-white tw-bg-blue-800 active:tw-bg-blue-700 tw-transition-colors tw-w-32 tw-py-1 focus:tw-outline-none tw-font-light" onClick={handleClearSignature}>
                     
                  </button>
                  <button className="tw-rounded tw-text-white tw-bg-blue-800 active:tw-bg-blue-700 tw-transition-colors tw-w-32 tw-py-1 tw-ml-3 focus:tw-outline-none tw-font-light" onClick={() => setSigModalOpen(false)}>
                     
                  </button>
               </div>
            </div>
         </ModalWindow>
      </div>
   )
}

const classSignature = 'tw-w-52 tw-h-16 tw-border tw-rounded tw-border-gray-400 tw-cursor-pointer print-border-bottom'
