import React,{useEffect, useState,useRef, useContext} from 'react'
import styled from 'styled-components'
import {fontSize, textColor,InputStyle,ColorRgb,NextBtn } from '../theme'
import {AiOutlineSend} from 'react-icons/ai'
import UserContext from 'context/UserContext'
import axios from 'axiosbase'
import AccessToken from 'context/accessToken'

function Feedback() {
    const ctx = useContext(UserContext);
    const [ btn, Spin ] = useState(false);
    const [ data, setData ] = useState(null);
    const [FinalErrorText, setFinalErrorText] = useState("");
    const [opacity2, setOpacity2] = useState("0");
    const [otherOne, setOtherOne] = useState({Cname: "getInputt", checked:null, self:""  });
    const [otherTwo, setOtherTwo] = useState({Cname: "getInputt", checked:null, self:"" });
    let inputFullName = useRef(null);
    useEffect(()=>{
        axios.get(`feedbacks`, { headers: { Authorization: AccessToken() } }).then(res=>{
            console.log(res, " my res");
            if(res.data.data.id){ setData(res.data.data) }else{ setTimeout(()=>{  inputFullName.current.focus();},3000); }
        }).catch(err=> setTimeout(()=>{  inputFullName.current.focus();},3000) )
    },[btn]);

    const onChange1 = (e) =>{
        if(e.target.value.length > 1){ setOtherOne({ Cname:"", checked:false, self:"getInputt" })
        }else{setOtherOne({ Cname:"getInputt", checked:null, self:"" })}
    }
    const onChange2 = (e) =>{
        if(e.target.value.length > 1){ setOtherTwo({ Cname:"", checked:false, self:"getInputt" })
        }else{setOtherTwo({ Cname:"getInputt", checked:null, self:"" })}
    }
    const reasonNo = (el) =>{
        if(el.target.value.length > 1){
            setOtherTwo({ Cname:"", checked:false, self:"" })
            el.target.className ="getInputt"
        }else{ el.target.className ="zz"; setOtherTwo({ Cname:"getInputt", checked:null, self:"" })  }
    }

    const ClickHandle = () =>{
        let inp = document.querySelectorAll(".getInputt"); let arr = Array.from(inp); let final = {};
        arr.map(el=>{
            if(el.type === "radio"){
                if(el.checked === true){
                    let next = document.querySelectorAll(`.${el.name}${el.id}_why`); let otherArr = Array.from(next);
                    otherArr.map(elem=>{
                        if(elem.id){
                             final[elem.name] = elem.value;
                        }else{
                            final[elem.name] = elem.value;
                        }
                    });
                    final[el.name] = el.value;
                }
            }else{
                if(el.name==="financing_checked"){
                     final[el.name] = el.value
                }else if(el.name==="finance_req"){
                     final[el.name] = el.value;
                } else if(el.name==="finance_req_no"){
                     final["finance_req"] = el.value;
                }else{
                    if(!el.value){
                        el.classList += " red"
                    }else{
                        final[el.name] = el.value;
                        el.classList =- " red"
                        el.classList += " getInputt"
                    }
                }
            }
        });

        let getT = document.querySelectorAll(".getTable1"); let tableArr = Array.from(getT); let tableOne = [];
        tableArr.map((el,i)=>{
            let obj = {};
            let tb = document.querySelectorAll(`.tableItem${i+1}`); let itemarr = Array.from(tb);
            itemarr.map((elem,ind)=>{
                if(elem.checked===true){
                    obj["point"] = parseInt(elem.value);
                    obj["title"] = elem.name;
                }
            });
            tableOne.push(obj);
        });
        let getT2 = document.querySelectorAll(".getTable2"); let tableArr2 = Array.from(getT2); let tableTwo = [];
        tableArr2.map((el,i)=>{
            let obj = {}; obj["title"] = el.id; let tb2 = document.querySelectorAll(`.itemOne${i+1}`); let itemarr2 = Array.from(tb2); 
            itemarr2.map((elem,index)=>{
                obj[elem.name] = elem.value
            });
            tableTwo.push(obj);
        })

        final["feedbackdetails"] = tableOne;
        final["efficiency"] = tableTwo;
        let keys = Object.keys(final); 
        console.log(keys.length);

        if(keys.length < 14){
            setFinalErrorText("   ..."); setOpacity2("1");
        }else{
            setOpacity2("0");
            axios.post('feedbacks', final, { headers: { Authorization: AccessToken() } }).then(res=>{
                console.log(res); ctx.alertText('green', " ", true); Spin(true);
            }).then(err=>{ ctx.alertText('orange', " ", true); Spin(true); })
        }
        console.log(final, "^final");
    }

    return (
        <FeedBackCont className="container">
            {!data?<div className="contentPar">
                        <div className="TitlePar">
                                <div className="title">   ( <span style={{color:`green`}}></span> ) </div>
                        </div>
                    </div> : 
            (<div className="contentPar">
                <div className="TitlePar">
                    <div className="title">  </div>
                    <div className="desc">       ,                   .      .  </div>
                </div>

                <div className="compName">
                    <div className="title">  /   :</div>
                    <InputStyle  className="nameText"><input className="getInputt" name="compname" ref={inputFullName} placeholder="   ..."  type="text" />  <div className="line"></div></InputStyle>
                </div>

                <div className="infoWhere">
                        <div className="Title"><span className="circle"></span>        ?</div>
                        <div className="helperTitle">[]     .</div>
                        <div className="inpPar">
                            {infoWhere.map((el,i)=>{
                                    return(
                                        <div className="items">
                                            <input style={{backgroundColor:"#eee"}} className="getInputt radio" id={i + 1} name="infhear_checked" value={el.title} type="radio" />
                                            <div className="title">{el.title}</div>
                                            {el.place&&<InputStyle className="nameText"><input placeholder={el.place} className={`infhear_checked${i + 1}_why`} name="infhear_other" id={i + 1}  type="text" /> <div className="line"></div></InputStyle>}
                                        </div>
                                    )
                            })}
                        </div>
                </div>

                <div className="infoWhere">
                    <div className="Title Title2"><span className="circle"></span>        ?
                        <InputStyle className="nameText"><input className="getInputt" name="edpcount" placeholder=" ..." type="number" />  <div className="line"></div></InputStyle>
                    </div>
                </div>

                <div className="infoWhere">
                    <div className="Title"><span className="circle"></span>           ? <br />  /   / :</div>
                    <div className="inpPar">
                        {infoWhere2.map((el,i)=>{
                                return(
                                    <div className="items">
                                        <input className={`radio ${otherOne.Cname}`} value={el.title} checked={otherOne.checked} name="financing_checked" type="radio" />
                                        <div className="title">{el.title}</div>
                                    </div>
                                )
                        })}
                        {/* zasna  */}
                        <div className="items">
                            <div className="title"> :</div>
                            <InputStyle className="nameText"><input className={otherOne.self} onChange={onChange1} name="financing_checked" placeholder="..." type="text" /> <div className="line"></div></InputStyle>
                        </div>
                    </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>         ? </div>
                        <div className="chekcPar">
                            <span className="title">   . [] : </span>
                            <div className="checkItem">
                                <div className="item"><input className="radio getInputt" type="radio" name="nofinancing_checked" value="true" /> <span></span></div>  
                                <div className="item"><input className="radio getInputt" type="radio" name="nofinancing_checked" value="false" /> <span></span></div>
                            </div>
                        </div>
                        <div className="Title3">
                            <div className="text">?</div> 
                            <InputStyle className="nameText"><textarea className="nofinancing_checked_why" name="nofinancing_reason" placeholder="  ..."  type="text" />  <div className="line"></div></InputStyle>
                        </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>     ,<br />: 1 ( ), 2 (), 3 (), 4 (), 5 ( )  6 ( ) </div>
                        <div className="ContentPar">

                            <table id="customers">
                                <tr>
                                  <th></th> <th> ,  </th><th>1</th><th>2</th> <th>3</th><th>4</th><th>5</th><th>6</th>
                                </tr>
                                {tableData.map((el,i)=>{
                                    return(
                                        <tr className="getTable1">
                                            <td>{i + 1}</td>
                                            <td>{el.title}</td>
                                            <td><input className={`radio tableItem${i+1}`} value={1} name={el.title} type="radio" /></td>
                                            <td><input className={`radio tableItem${i+1}`} value={2} name={el.title} type="radio" /></td>
                                            <td><input className={`radio tableItem${i+1}`} value={3} name={el.title} type="radio" /></td>
                                            <td><input className={`radio tableItem${i+1}`} value={4} name={el.title} type="radio" /></td>
                                            <td><input className={`radio tableItem${i+1}`} value={5} name={el.title} type="radio" /></td>
                                            <td><input className={`radio tableItem${i+1}`} value={6} name={el.title} type="radio" /></td>
                                        </tr>
                                    )
                                })}
                               
                            </table>
                        </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>           ?          .</div>
                        <div className="ContentPar">
                            <table id="customers">
                                <tr>
                                  <th></th>
                                   <th>  </th>
                                   <th ><span className="question"> []</span> []<span className="question"> []</span><span className="question"> []</span></th>
                                   <th>      ?</th>
                                </tr>
                                {tableData2.map((el,i)=>{
                                    return(
                                        <tr id={el.title} className="getTable2">
                                            <td>{i+1}</td>
                                            <td>{el.title}</td>
                                            <td ><InputStyle className="nameText"><input className={`itemOne${i+1}`} name="appoint_answer"  placeholder=" ..."  type="text" />  <div className="line"></div></InputStyle></td>
                                            <td ><InputStyle className="nameText"><input className={`itemOne${i+1}`} name="amount" style={{textAlign:`right`}} placeholder=" ..."  type="text" />  <div className="line"></div></InputStyle></td>
                                        </tr>
                                    )
                                })}
                            </table>
                        </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>                   ?</div>
                        <div className="chekcPar">
                            <span className="title">    [] : </span>
                            <div className="checkItem">
                                <div className="item"><input className="radio getInputt" name="direct_checked" value="true" type="radio" /> <span></span></div>  
                                <div className="item"><input className="radio getInputt" name="direct_checked" value="false" type="radio" /> <span></span></div>
                            </div>
                        </div>
                        <div className="Title3">
                            <div className="text">?</div> 
                            <InputStyle className="nameText"><textarea name="direct_reason" className="direct_checked_why" placeholder="  ..."  type="text" />  <div className="line"></div></InputStyle>
                         </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>  12        ?</div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>       []?</div>
                        <div className="chekcPar">
                            <div className="checkItem">
                                <div style={{marginLeft:0}} className="item">
                                    <input className={`radio ${otherTwo.Cname}`} checked={otherTwo.checked} name="finance_req" type="radio" value=", " /> <span>1. ,   </span></div>  
                                <div className="item">
                                    <input className={`radio ${otherTwo.Cname}`} checked={otherTwo.checked} type="radio" value=" " name="finance_req" /> <span>2.   </span></div>
                                <div className="item"><span>3.   </span><InputStyle style={{marginLeft:10}} className="nameText">
                                    <input onChange={onChange2} name="finance_req" placeholder="  ..." className={otherTwo.self}  type="text" />  <div className="line"></div></InputStyle></div>
                            </div>
                        </div>

                        <div className="Title3">
                            <div style={{width:"30%"}} className="text">   ?</div> 
                            <InputStyle className="nameText"><textarea onChange={reasonNo} name="finance_req_no" placeholder="  ..." type="text" />  <div className="line"></div></InputStyle>
                         </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>                     ? [] </div>
                        <div className="chekcPar">
                            <span className="title">   . [] : </span>
                            <div className="checkItem">
                                <div className="item"><input className="radio getInputt" type="radio" value="true" name="tofinance_checked" /> <span>, 100% .</span></div>  
                                <div className="item"><input className="radio getInputt" type="radio" value="false" name="tofinance_checked" /> <span></span></div>
                            </div>
                        </div>
                        <div className="Title3">
                            <div style={{width:"30%"}} className="text">  :?</div> 
                            <InputStyle className="nameText"><textarea  className={`tofinance_checked_why`} name="tofinance_reason" placeholder="  ..."  type="text" /><div className="line"></div></InputStyle>
                         </div>
                </div>

                <div className="infoWhere">
                        <div className="Title Title4"><span className="circle"></span>             ? [] </div>
                        <div className="chekcPar">
                            <span className="title">   . [] : </span>
                            <div className="checkItem">
                                <div className="item"><input className="radio getInputt" type="radio" name="edp_impression" value="true" /> <span></span></div>  
                                <div className="item"><input className="radio getInputt" type="radio" name="edp_impression" value="false" /> <span></span></div>
                            </div>
                        </div>
                </div>



                <div className="buttonPar">
                    <div style={{opacity:`${opacity2}`}} className="errtext">{FinalErrorText}</div>
                    <NextBtn onClick={ClickHandle} className="SubmitButton" type="button"> <div className="flexchild"><AiOutlineSend/><AiOutlineSend className="hide" /> <AiOutlineSend className="hide1" /></div></NextBtn>
                </div>
            </div>
            )}
            
        </FeedBackCont>
    )
}


export default Feedback

// #1890ff;
const FeedBackCont = styled.div`
    color: rgba(${textColor});
    padding-bottom:80px;
    .form-control{
       &:hover{
           border:1px solid #1890ff;
       }
    }
    .contentPar{
        background-color:white;
        padding:20px 100px;
        font-size:${fontSize};
        margin-top:30px;
        border:1px solid rgba(0,0,0,.2);
        .TitlePar{
            padding:10px 0px;
            .title{
                color:${textColor};
                padding-bottom:10px;
                font-size:14px;
                text-align:center;
                font-weight:500;
            }
            .desc{
                font-style: italic;
            }
        }
        .compName{
            width:100%;
            padding:10px 0px;
            display:flex;
            margin-bottom:16px;
            .title{
                width:50%;
                
            }
            .nameText{
                width:100%;
            }
        }
        
        .infoWhere{
            width:100%;
            margin-bottom:40px;
            #customers {
                border-collapse: collapse;
                width: 100%;
                td, th{
                    border: 1px solid rgba(0,0,0,0.2);
                    padding: 8px;
                }
                th{
                    color:white;
                    background-color:rgba(${ColorRgb});

                    .question{
                        opacity:0;
                        transform:scale(0);
                        // margin-right:80px;
                    }
                }
                td{
                    .radio{
                        cursor:pointer;
                        width:15px;
                        height:15px;
                        transition:all 0.3s ease;
                        margin-right:5px;
                        &:checked{
                            -webkit-transform: scale(1.2);
                            transform: scale(1.2);
                            border-radius:50% !important;
                        }
                    }
                    .nameText{
                        width:100%;
                        .line{
                            height:2px;
                            background-color:#1890ff;
                        }
                    }
                }
               
            }
            .Title{
                font-weight:500;
                display:flex;
                align-items:center;
                .circle{
                    margin-right:10px;
                    font-size:7px;
                }
            }
            .Title2{
               display:flex;
               align-items:center;
               .nameText{
                   width:35%;
                   font-size:13px;
                    margin-left:15px;
               }
            }
            .Title3{
                padding-top:10px;
                padding-left:40px;
                display:flex;
                .text{
                    width:10%;
                }
                .nameText{
                    width:90%;
                }
            }
            .Title4{
                margin-bottom:8px;
            }
            .helperTitle{
                color: rgba(${textColor},0.8);
                margin-bottom:6px;
            }
            .chekcPar{
                padding-left:40px;
                display:flex;
                .title{

                }
                .checkItem{
                    display:flex;
                    .item{
                        display:flex;
                        align-items:center;
                        margin-left:25px;
                        .radio{
                            cursor:pointer;
                            width:15px;
                            height:15px;
                            transition:all 0.3s ease;
                            margin-right:5px;
                            &:checked{
                                -webkit-transform: scale(1.2);
                                transform: scale(1.2);
                                border-radius:50% !important;
                            }
                        }
                    }
                }
            }
            .inpPar{
                padding-left:40px;
                color: rgba(${textColor},0.95);
                .items{
                    padding:7px 0px;
                    width:100%;
                    display:flex;
                    justify-content:start;
                    align-items:center;
                    .radio{
                        cursor:pointer;
                        width:15px;
                        height:15px;
                        transition:all 0.3s ease;
                        margin-right:15px;
                        &:checked{
                            -webkit-transform: scale(1.2);
                            transform: scale(1.2);
                            border-radius:50% !important;
                        }
                    }
                    .title{
                        margin-right:8px;
                        // width:60%;
                    }
                    .nameText{
                        font-size:13px;
                        width:40%;
                    }
                }
            }
        }
    }
    .buttonPar{
        margin:10px 0px;
        display:flex;
        flex-direction:row;
        align-items:center;
        justify-content:space-between;
          .errtext{
            transition:all 0.4s ease;
            text-align:center;
            background-color: #f6c343;
            border-radius:5px;
            font-size:15px !important;
            font-weight:400;
            color:black !important;
            line-height:34px;
            padding:0px 20px;
          }
      }

    @media only screen and (max-width:786px){
        .contentPar{
            padding: 20px 10px
        }
    }
`

const infoWhere = [
    { title: "   ,  () ", place: "   "},
    { title: "  (  )", place: "  ..." },
    { title: "   ,  ()  ", place: "..."},
    { title: " ( )", place: "   ..."},
    { title: "  ( )", place: "   ..."},
    { title: "    ", place: null },
    { title: "   ", place: null },
    { title: "  ", place: null },
    { title: "   ", place: null },
    { title: "   ( ) ", place: " ..."},
]

const infoWhere2 = [
    {  title: ", ", place: null },
    {  title: "  ", place: null},
    {  title: ",  ,  ", place: null },
    {  title: " ", place: null },
    {  title: " 	", place: null},
    {  title: ",   ", place: null },
    {  title: "  ,  ", place: null},
    {  title: "- , ", place: null},
    // {  title: ":", place: "..." },
]

const tableData = [
    {  title: "       ?", name: "tablerow1" },
    {   title: "  ,     ,     ?", name: "tablerow2" },
    {   title: "      ,     ?", name: "tablerow3" },
    {   title: "     ,    ?", name: "tablerow4" },
    {   title: "        ?", name: "tablerow5" }
]


const tableData2 = [
    {  title: " " },
    {   title: " "},
    {   title: "   "},
    {   title: " "},
    {   title: "    "},
    {   title: "    "},
    {   title: "  "},
    {   title: "   "},
    {   title: "   "},
    {   title: "  "},
    {   title: "  "},
    {   title: ",   :"},
]