import React from 'react'
import { Provision } from './makeContract'

const contract = {
   1: {
      category: '',
      provisions: [{
         order: '1.1',
         provision: '  (  )-                 .',
         subLevel: 0
      }]
   },
   2: {
      category: '',
      provisions: [{
         order: '2.1',
         provision: '   (     );  , ,   ;   ,  ,    ,    (  );              ,        ,  ,     .',
         subLevel: 0
      }, {
         order: '2.2',
         provision: '   :',
         subLevel: 0
      }, {
         order: '2.2.1',
         provision: '       . :',
         subLevel: 1
      }, {
         order: 'i',
         provision: '                       , ,  ,   ;',
         subLevel: 2
      }, {
         order: 'ii',
         provision: '                    ,    ,         ;',
         subLevel: 2
      }, {
         order: 'iii',
         provision: '                            ;',
         subLevel: 2
      }, {
         order: 'iv',
         provision: '           ,             ,    .',
         subLevel: 2
      }, {
         order: 'v',
         provision: '   :',
         subLevel: 2
      }, {
         order: '1',
         provision: '     ,  , ,  ,  ,  ,   ,                       ;                , , ,          ;',
         subLevel: 3
      }, {
         order: '2',
         provision: '           4- 2.2 -                 .',
         subLevel: 3
      }]
   }
}

export default function ContractAttach6() {
   return (
      <div className="tw-text-sm tw-text-gray-700 tw-py-6 print-break-after">
         <div className="tw-text-base tw-font-medium tw-text-center tw-mt-6 tw-mx-2 sm:tw-mx-8">
             6. ,       ,  ,     
         </div>

         <div className="tw-font-light tw-text-center tw-mt-2 tw-mx-4 sm:tw-mx-12">
            (      )
         </div>

         <div className="tw-mx-2 sm:tw-mx-8 tw-mt-8 tw-pb-10">
            {Object.entries(contract).map(([key, value]) =>
               <div className="" key={key}>
                  <Header header={`${key}  . ${value.category?.toUpperCase()}`} key={key} />

                  {value.provisions.map(provision =>
                     <Provision
                        order={provision.order}
                        provision={provision.provision}
                        subLevel={provision.subLevel}
                        key={provision.order}
                     />
                  )}
               </div>
            )}
         </div>
      </div>
   )
}

function Header({ header }) {
   return <div className="tw-font-medium tw-mt-2 tw-mx-2">
      {header}
   </div>
}
