import React, { useEffect } from 'react'
import { Document, Page, Text, View, StyleSheet, Font ,Image } from '@react-pdf/renderer';
import { styles, HeadImage } from "components/misc/PdfStyle"


const ResultMeasurePdf = ({ wait, data, years, lang, title }) => {
    // useEffect(()=>{
    //     GlobalFont();
    // },[])

    return (
        <Document title={title} >
            {wait?
            <>
                <PdfTemplate wait={wait} title={title} data={data.filter(item=>item.row_type==="level1").sort((a,b)=>a.row_number-b.row_number)} years={years} page="1" lang={lang} />
                <PdfTemplate wait={wait} title={title} data={data.filter(item=>item.row_type==="level2").sort((a,b)=>a.row_number-b.row_number)} years={years} page="2" lang={lang} />
            </>
           :<Page wrap={false} size="A4">
                <Text>Loading...</Text>
            </Page>}
        </Document>
    )
}

export default ResultMeasurePdf

let today = new Date();
let dd = String(today.getDate()).padStart(2, '0');
let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
let yyyy = today.getFullYear();

today = yyyy + '/' + dd + '/' + mm;

const PdfTemplate = ({ title, data, years, page, lang }) =>{
    return( 
       
        <Page orientation="landscape" wrap size="A4" style={styles.page}>
            <View fixed style={styles.headerStyle}>
                <Text >   ,   - {page}</Text>
            </View>
            <View style={styles.headTitle}>
                <Text > </Text> 
                <Text >  </Text> 
            </View>
            <View style={styles.headTitle}>
                <Text >   </Text> 
                <Text style={{fontWeight:'light'}}> : 2017/01/01 - {today}</Text> 
            </View>
            <View style={styles.tableParent}>
                <View style={[styles.tableRow, styles.tableHead, {borderBottom:'none'}]}>
                        <View style={[styles.tableCol, {width:'2%',borderColor:"#fff"}]}>
                            <Text ></Text> 
                        </View>
                        <View style={[styles.tableCol, {width:'20%',borderColor:"#fff"}]}>
                            <Text></Text> 
                        </View>
                        <View style={styles.tableCol}>
                            <Text></Text> 
                        </View>
                        {years.map((el,ind)=>{
                        return(
                            <React.Fragment key={ind}>
                                <View style={[styles.tableCol, {width:'10.4%', borderBottom:0.5}, ind===years.length - 1?{borderRightColor:'#fff'}:{} ]}><Text break>{el.year}</Text></View>
                            </React.Fragment>
                        )
                        })}
                </View>
                <View style={[styles.tableRow, styles.tableHead]}>
                        <View style={[styles.tableCol, {width:'2%', borderColor:'#fff'}]}>
                            <Text ></Text> 
                        </View>
                        <View style={[styles.tableCol, {width:'20%', borderColor:'#fff'}]}>
                            <Text> </Text> 
                        </View>
                        <View style={styles.tableCol}>
                        <Text></Text> 
                        </View>
                        {years.map((el,ind)=>{
                        return(
                            <React.Fragment key={ind}>
                                <View style={styles.tableCol}><Text break>Cumalative</Text></View>
                                <View style={[styles.tableCol, ind===years.length - 1?{borderColor:'#fff'}:{borderColor:'#000'}]}><Text break>Current</Text></View>
                            </React.Fragment>
                        )
                        })}
                </View>
                {data.map((el,ind)=>{
                    return(
                        <View style={[styles.tableRow, ind===data.length - 1?{borderColor:'#fff'}:{borderColor:'#000'}]} key={ind}>
                            <View style={[styles.tableCol, ,{width:'2%'}]}><Text>{el.row_number}</Text></View>
                            <View style={[styles.tableCol,{width:'20%', textAlign:'left'}]}><Text break >{el[`description_${lang}`]}</Text></View>
                            <View style={styles.tableCol}><Text>{el.measure}</Text></View>
                            {years.map((elem,index)=>{
                                return(
                                    <React.Fragment key={index}>
                                        <View style={styles.tableCol}><Text>{el[`cum${elem.year}`]!==null?el[`cum${elem.year}`]:'-'} {el.measure===""&&el[`cum${elem.year}`]?` %`:``}</Text></View>
                                        <View style={[styles.tableCol, index===years.length - 1?{borderColor:'#fff'}:{borderColor:'#000'}]}><Text>{el[`cur${elem.year}`]}</Text></View>
                                    </React.Fragment>
                                )
                            })}
                        </View>
                    )
                })}
            </View>
            
            <View style={styles.InfoParent}>
                <View style={styles.InfoRow}>
                    <Text style={styles.InfoCol}></Text>
                    <Image style={[styles.SigImageStyle, styles.InfoCol]} src={sig} />
                    <Text style={styles.InfoCol}>. / /</Text>
                </View>

                <View style={styles.InfoRow}>
                    <Text style={styles.InfoCol}></Text>
                    <Image style={[styles.SigImageStyle, styles.InfoCol]} src={sig} />
                    <Text  style={styles.InfoCol}>. /  /</Text>
                </View>
            </View>
        </Page> 
    
    )
}

const sig = 'data:image/png;base64,'